import { useState } from "react";
import "./Contact.scss";
import emailjs from 'emailjs-com';

export default function Contact() {
  const [message, setMessage] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    emailjs.sendForm('service_6ngutu9', 'template_khze98d', e.target, 'user_TeyYkNUXkv6XfKC8Lv4GS')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
       setMessage(true);
      e.target.reset();
    };
  return (
    <div className="contact" id="contact">
      <div className="left">
        <img src="assets/shake.svg" alt="" />
      </div>
      <div className="right">
        <h2>Contact Me</h2>
        <form onSubmit={handleSubmit}>
        <input type="text" placeholder="Name" name="name" id="name" />
          <input type="text" placeholder="Email"  name="to_email" id="to_email" />
          <input type="text" placeholder="Mobile No" name="to_phone" maxLength="10" id="to_phone" />
          
          <textarea placeholder="Message" name="message" id="message"></textarea>
          <button type="submit">Send</button>
          {message && <span>Thanks, I'll reply ASAP :)</span>}
        </form>
      </div>
    </div>
  );
}
