import { useState } from "react";
import "./Works.scss";

export default function Works() {
  const [currentSlide, setCurrentSlide] = useState(0);
  const data = [
    {
      id: "1",
      //icon: "./assets/mobile.png",
      icon: "./assets/globe.png",
      title: "Thyrocare Booking Website",
      desc:"Public booking website designed for the Health Care Product Booking with Implemented Coupon System for the Offer Booking.",
      img: "https://i.ibb.co/Q6b8tt3/Wellness1.png",
      Link:"https://www.thyrocare.com",
    },
    {
      id: "2",
      icon: "./assets/globe.png",
      title: "Covid Website",
      desc: "Specially Designed for the Covid Products Booking , Implemented State wise Rate Capping.",
      img:"https://i.ibb.co/0cK20cG/Covid1.png",
      Link:"https://covid.thyrocare.com",
    },
    {
      id: "3",
      icon: "./assets/globe.png",
      title: "Client Portal",
      desc: "Thyrocare Client Booking Portal for their Customers",
      img: "https://i.ibb.co/CmfzMDV/DSA1.png",
      Link:"https://client.thyrocare.cloud",
    },
  ];

  const handleClick = (way) => {
    way === "left"
      ? setCurrentSlide(currentSlide > 0 ? currentSlide - 1 : 2)
      : setCurrentSlide(currentSlide < data.length - 1 ? currentSlide + 1 : 0);
  };
  
  return (
    <div className="works" id="works">
      <div
        className="slider"
        style={{ transform: `translateX(-${currentSlide * 100}vw)` }}
      >
        {data.map((d) => (
          <div className="container">
            <div className="item">
              <div className="left">
                <div className="leftContainer">
                  <div className="imgContainer">
                    <img src={d.icon} alt="" />
                  </div>
                  <h2>{d.title}</h2>
                  <p>{d.desc}</p>
                 <a href={d.Link} target="_blank"> <span>Link</span></a>
                </div>
              </div>
              <div className="right">
                <img
                  src={d.img}
                  alt=""
                />
              </div>
            </div>
          </div>
        ))}
      </div>
      <img
        src="assets/arrow.png"
        className="arrow left"
        alt=""
        onClick={() => handleClick("left")}
      />
      <img
        src="assets/arrow.png"
        className="arrow right"
        alt=""
        onClick={() => handleClick()}
      />
    </div>
  );
}
